import React from "react";
import FormSection from "./FormSection";
import {makeNoboField} from "./NoboField";
import Input from "../components/Input";
import InputDate from "../components/InputDate";
import Select from "../components/Select";
import {connect} from "react-redux";
//import RadiosFunc from "../components/RadiosFunc";
import {lookup} from '../startRedux';

const FishType = makeNoboField('fishType',Select,{ options: 'country' });
const DogType = makeNoboField('dogType',Input,{ maxLength: 6 });
const EggType = makeNoboField('eggType',Input,{ maxLength: 80 });
const CatType = makeNoboField('catType',Input,{ maxLength: 80 });
const PurchaseDate = makeNoboField('purchaseDate',InputDate,{});

export class NoboTest extends React.PureComponent {

    getId () {
        return this.props.id;
    }

    render () {
        const { values } = this.props;
        const { fishType } = values;
        return (
            <FormSection section={this}>
                <FishType/>
                {fishType==='OTH' && <DogType/>}
                <EggType foo={fishType}/>
                <CatType/>
                <PurchaseDate/>
            </FormSection>
        );
    }
}

function mapStateToProps (state,props) {
    //const fishType = state.formState[props.id + '-fishType'] || '';
    //const eggType = state.formState[props.id + '-eggType'] || '';

    const values = lookup([props.id],state.formState);
    //console.log('MSP',props,state);
    return {
        //fishType,
        //eggType,
        values
    };
}

export default connect(mapStateToProps)(NoboTest);



